// --------------------------------------------
// --------------------------------------------
//
// Element :: Page Header
//
// --------------------------------------------
// --------------------------------------------



// Settings
// --------------------------------------------
$page_header_ht:  67px;
$page_header_bg: $ui-dark-medium;
$page_header_link_active: lighten($ui-dark-medium, 10%);
$bp_page_header_filter: 40rem;




// Placeholders
// --------------------------------------------
%page-header__pad {
  padding-top: $spacer-xsmall;
  padding-bottom: $spacer-xsmall;
}




// Styles
// --------------------------------------------
.#{$css-prefix}page-header {
  @include clearfix;
  background-color: $page_header_bg;
  padding-left: $spacer-small;
  padding-right: $spacer-small;
  line-height: $page_header_ht;
}


.#{$css-prefix}page-header__h {
  @extend %display--med;
  text-transform: uppercase;
  color: #fff;
  float: left;
}


.#{$css-prefix}page-header__nav {
  display: none;
  clear: left;
  margin-bottom: $spacer-medium;

  @include mq($bp_page_header_filter) {
    display: block;
    clear: none;
    float: left;
    margin-left: $spacer-medium;
    margin-bottom: 0;
  }

  // When Page header is active
  .page-header__filter--is-active & {
    display: block;
  }
}



.#{$css-prefix}page-header__items {
  @include clearfix;
  @extend %list__items;
}



.#{$css-prefix}page-header__item {
  line-height: $page_header_ht;
  @include mq($bp_page_header_filter) {
    float: left;
  }
}



.#{$css-prefix}page-header__link {
  display: block;
  padding-left: 12px;
  padding-right: 12px;
  color: darken(#fff, 25%);
  &:hover { color: #fff; }
  &--is-active {
    background-color: $page_header_link_active;
  }
}


.#{$css-prefix}page-header__trigger {
  display: block;
  float: right;

  @include mq($bp_page_header_filter) {
    display: none;
  }
}