// --------------------------------------------
// --------------------------------------------
//
// Element :: Modal
//
// --------------------------------------------
// --------------------------------------------


// Config
// --------------------------------------------
$modal-pad-large:      1rem;
$modal-pad-medium:     0.75rem;
$modal-pad-msmall:      0.55rem;

$modal-pad-side:    $modal-pad-large;

$modal-color-dark:  $ui-text-dark;
$modal-color-medium:   $ui-text-medium;

$modal-color-text:  $ui-text-medium;

$modal-color-overlay: $ui-dark;








// Styles
// --------------------------------------------

.#{$css-prefix}modal__overlay {
  position: absolute;
  z-index: $zindex-overlay;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  background: $modal-color-overlay;
  opacity: 0;
  visibility: hidden;
  transition: visibility 400ms linear, opacity 400ms cubic-bezier(0.4, 0, 0.2, 1);
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  text-align: center;


  // Modal is visible
  .ko-modal--is-visible & {
    opacity: 0.98;
    visibility: visible;
  }
}


.#{$css-prefix}modal__container {
  position: fixed;
  display: flex;
  align-items: center;
  left: 0;
  top: 0;
  z-index: $zindex-overlay;
  width: 100%;
  height: 100%;
  background-color: transparent;
  visibility: hidden;
  backface-visibility: hidden;
  opacity: 0;
  text-align: center;
  transition: opacity 200ms ease;

  // overlay
  .ko-modal--is-visible & {
    visibility: visible;
    opacity: 1;
    overflow: scroll;
  }
}


.#{$css-prefix}modal {
  display: inline-block;
  position: relative;
  z-index: $zindex-modal;
  width: 100%;
  max-width: 400px;
  background-color: #fff;
  border-radius: 1px;
  box-shadow: 5px 5px 25px rgba(0, 0, 0, 0.15);
  overflow: hidden;
  opacity: 1;
  text-align: left;
  transform: translateY(50%);
  transition: all 400ms ease;
  margin: 0 auto;

  // This class is added to the <body>
  .ko-modal--is-visible & { transform: translateY(0); }
}


.#{$css-prefix}modal__title-bar {
  @include backgrounder($ui-dark-medium);
  position: relative;
  padding: $modal-pad-medium $modal-pad-side;
}


.#{$css-prefix}modal__title {
  margin: 0;
  font-size: $font-size-base;
  text-transform: uppercase;
  color: #fff;
  color: rgba(#fff, 0.9);
}


.#{$css-prefix}modal__meta {
  padding: $modal-pad-large $modal-pad-side 0;
}


.#{$css-prefix}modal__content {
  padding: 0 $modal-pad-side;
}


.#{$css-prefix}modal__action {
  @include clearfix;
  padding: $modal-pad-medium $modal-pad-side;
  background-color: $ui-gray-light;
}


.#{$css-prefix}modal__h {
  color: $modal-color-dark;
  font-size: $display-size-h3;
  margin: 0;
}


.#{$css-prefix}modal__h--sub {
  color: $modal-color-text;
  font-size: $body-size-medium;
  margin: 0;
}


.#{$css-prefix}modal__text {
  color: $modal-color-text;
  font-size: $body-size-medium;
  margin: 1rem 0;
}





