// --------------------------------------------
// --------------------------------------------
//
// Utilities
//
// --------------------------------------------
// --------------------------------------------


// Floats
// --------------------------------------------
.u-float--left  { float: left; }
.u-float--right { float: right; }


// Content Alignment
// --------------------------------------------
// Note: Left Aligned is Default

.u-align--right { text-align: right; }
.u-align--center { text-align: center; }


// Spacers :: All
// --------------------------------------------
.#{$css-prefix}spacer--xxsm { margin: $spacer-xxsmall; }
.#{$css-prefix}spacer--xsm { margin: $spacer-xsmall; }
.#{$css-prefix}spacer--sm { margin: $spacer-small; }
.#{$css-prefix}spacer--med { margin: $spacer-medium; }
.#{$css-prefix}spacer--lg { margin: $spacer-large; }
.#{$css-prefix}spacer--xlg { margin: $spacer-xlarge; }
.#{$css-prefix}spacer--xxlg { margin: $spacer-xxlarge; }


// Spacers :: Vertical
// --------------------------------------------

// Top + Bottom
.#{$css-prefix}spacer--xxsm--vert { margin-top: $spacer-xxsmall; margin-bottom: $spacer-xxsmall; }
.#{$css-prefix}spacer--xsm--vert { margin-top: $spacer-xsmall; margin-bottom: $spacer-xsmall; }
.#{$css-prefix}spacer--sm--vert{ margin-top: $spacer-small; margin-bottom: $spacer-small; }
.#{$css-prefix}spacer--med--vert { margin-top: $spacer-medium; margin-bottom: $spacer-medium; }
.#{$css-prefix}spacer--lg--vert { margin-top: $spacer-large; margin-bottom: $spacer-large; }
.#{$css-prefix}spacer--xlg--vert { margin-top: $spacer-xlarge; margin-bottom: $spacer-xlarge; }
.#{$css-prefix}spacer--xxlg--vert { margin-top: $spacer-xxlarge; margin-bottom: $spacer-xxlarge; }

// Bottom
.#{$css-prefix}spacer--xxsm--btm { margin-bottom: $spacer-xxsmall; }
.#{$css-prefix}spacer--xsm--btm { margin-bottom: $spacer-xsmall; }
.#{$css-prefix}spacer--sm--btm { margin-bottom: $spacer-small; }
.#{$css-prefix}spacer--med--btm {margin-bottom: $spacer-medium; }
.#{$css-prefix}spacer--lg--btm { margin-bottom: $spacer-large; }
.#{$css-prefix}spacer--xlg--btm { margin-bottom: $spacer-xlarge; }
.#{$css-prefix}spacer--xxlg--btm { margin-bottom: $spacer-xxlarge; }

// Top
.#{$css-prefix}spacer--xxsm--top { margin-top: $spacer-xxsmall; }
.#{$css-prefix}spacer--xsm--top { margin-top: $spacer-xsmall; }
.#{$css-prefix}spacer--sm--top { margin-top: $spacer-small; }
.#{$css-prefix}spacer--med--top {margin-top: $spacer-medium; }
.#{$css-prefix}spacer--lg--top { margin-top: $spacer-large; }
.#{$css-prefix}spacer--xlg--top { margin-top: $spacer-xlarge; }
.#{$css-prefix}spacer--xxlg--top { margin-top: $spacer-xxlarge; }


// Spacers :: Horizontal
// --------------------------------------------

// Right + Left
.#{$css-prefix}spacer--xxsm--horiz { margin-right: $spacer-xxsmall; margin-left: $spacer-xxsmall; }
.#{$css-prefix}spacer--xsm--horiz { margin-right: $spacer-xsmall; margin-left: $spacer-xsmall;  }
.#{$css-prefix}spacer--sm--horiz { margin-right: $spacer-small; margin-left: $spacer-small;  }
.#{$css-prefix}spacer--med--horiz {margin-right: $spacer-medium; margin-left: $spacer-medium; }
.#{$css-prefix}spacer--lg--horiz { margin-right: $spacer-large; margin-left: $spacer-large;  }
.#{$css-prefix}spacer--xlg-horiz { margin-right: $spacer-xlarge; margin-left: $spacer-xlarge;  }
.#{$css-prefix}spacer--xxlg-horiz { margin-right: $spacer-xxlarge; margin-left: $spacer-xxlarge;  }

// Right
.#{$css-prefix}spacer--xxsm--right { margin-right: $spacer-xxsmall; }
.#{$css-prefix}spacer--xsm--right { margin-right: $spacer-xsmall; }
.#{$css-prefix}spacer--sm--right { margin-right: $spacer-small; }
.#{$css-prefix}spacer--med--right {margin-right: $spacer-medium; }
.#{$css-prefix}spacer--lg--right { margin-right: $spacer-large; }
.#{$css-prefix}spacer--xlg--right { margin-right: $spacer-xlarge; }
.#{$css-prefix}spacer--xxlg--right { margin-right: $spacer-xxlarge; }

// Left
.#{$css-prefix}spacer--xxsm--left { margin-left: $spacer-xxsmall; }
.#{$css-prefix}spacer--xsm--left { margin-left: $spacer-xsmall; }
.#{$css-prefix}spacer--sm--left { margin-left: $spacer-small; }
.#{$css-prefix}spacer--med--left {margin-left: $spacer-medium; }
.#{$css-prefix}spacer--lg--left { margin-left: $spacer-large; }
.#{$css-prefix}spacer--xlg--left { margin-left: $spacer-xlarge; }
.#{$css-prefix}spacer--xxlg--left { margin-left: $spacer-xxlarge; }


// Visually Hidden + Screen Reader
// --------------------------------------------
// These classes visually hide elements that we want to make available for screen reader users

.u-visually-hidden, %u-visually-hidden {
  position: absolute !important;
  margin: -1px !important;
  border: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  overflow: hidden !important;
  clip: rect(0 0 0 0) !important;
}

.u-sr-only { @extend %u-visually-hidden; }


// Visable at Breakpoints
// --------------------------------------------
// These all you to hide or show elements above, below, or at specific breakpoints

// Helper Classes for Hiding

.u-hide---small {
  display: block;

  @include mq(max, $bp-small) {
    display: none;
  }
}

.u-hide--medium {
  display: block;

  @include mq($bp-small, $bp-medium) {
    display: none
  }
}

.u-hide--mediumdown {
  display: block;

  @include mq(max, $bp-medium) {
    display: none;
  }
}

.u-hide--large {
  display: block;

  @include mq($bp-medium, $bp-large) {
    display: none;
  }
}

.u-hide--xlarge {
  display: block;

  @include mq(min, $bp-large) {
    display: none;
  }
}

// Helper Classes for Showing

.u-show--small {
  display: none;

  @include mq(max, $bp-small) {
    display: block;
  }
}

.u-show--medium {
  display: none;

  @include mq($bp-small, $bp-medium) {
    display: block;
  }
}

.u-show--mediumdown {
  display: none;

  @include mq(max, $bp-medium) {
    display: block;
  }
}

.u-show--large {
  display: none;

  @include mq($bp-medium, $bp-large) {
    display: block;
  }
}

.u-show--xlarge {
  display: none;

  @include mq(min, $bp-large) {
    display: block;
  }
}




