// Headings
// --------------------------------------------
.#{$css-prefix}display--h1, %display--h1 {
  font-family: $font-display;
  font-size: $display-size-h1;
  font-weight: $font-weight-bold;
  // text-transform: uppercase;
}

.#{$css-prefix}display--h2, %display--h2 {
  font-family: $font-display;
  font-size: $display-size-h2;
  font-weight: $font-weight-bold;
  // text-transform: uppercase;
}

.#{$css-prefix}display--h3, %display--h3 {
  font-family: $font-display;
  font-size: $display-size-h3;
  font-weight: $font-weight-bold;
}

.#{$css-prefix}display--h4, %display--h4 {
  font-family: $font-display;
  font-size: $display-size-h4;
  font-weight: $font-weight-bold;
}

.#{$css-prefix}display--h5, %display--h5 {
  font-family: $font-display;
  font-size: $display-size-h5;
  font-weight: $font-weight-bold;
}

.#{$css-prefix}display--h6, %display--h6 {
  font-family: $font-display;
  font-size: $display-size-h6;
  font-weight: $font-weight-bold;
}


// Use this to make a regular display face regular weight;
.#{$css-prefix}display--normal {
  font-weight: $font-weight-reg;
}




// Paragraphs/Body
// --------------------------------------------
.#{$css-prefix}text--xsm, %text--xsm {
  font-family: $font-body;
  font-size: $body-size-xsmall;
}

.#{$css-prefix}text--sm, %text--sm {
  font-family: $font-body;
  font-size: $body-size-small;
}

.#{$css-prefix}text--med, %text--med {
  font-family: $font-body;
  font-size: $body-size-medium;
}

.#{$css-prefix}text--lg, %text--lg {
  font-family: $font-body;
  font-size: $body-size-large;
  line-height: 1.3125;
}




// Articles
// --------------------------------------------
// Add this class to a container for continuous area of text, such as a blog post.
// This will automatically handle the space between paragraphs
.#{$css-prefix}article {
  > p { margin-top: 0.875rem;  margin-bottom: 0.875rem }
}








// Lists
// --------------------------------------------

// Basic List Style
// --------------------------------------------
// This is extended into all other lists to give that variation our basic styles
.#{$css-prefix}list__items, %list__items {
  margin: 0;
  padding: 0;
  list-style-type: none;
  color: $body-text-color;
}


.#{$css-prefix}list__items--3 {
  column-count: 3;
}


// List Style Types
// --------------------------------------------
.#{$css-prefix}list__items--square {
  @extend %list__items;
  list-style: square inside none;
}

.#{$css-prefix}list__items--circle {
  @extend %list__items;
  list-style: circle inside none;
}

.#{$css-prefix}list__items--disc {
  @extend %list__items;
  list-style: disc inside none;
}




// Fancy Numbered Lists
// --------------------------------------------
// Use this on <ol> elements to get fancy numbers that can be more easily styled
.#{$css-prefix}list__items--numbered {
  @extend %list__items;

  >.#{$css-prefix}list__item {
    counter-increment: step-counter;
    list-style: none;
    &:before {
      content: counter(step-counter) '.';
      margin-right: 5px;
      font-size: 80%;
      background-color: rgb(200,200,200);
      color: white;
      font-weight: bold;
      padding: 3px 8px;
      border-radius: 3px;
    }
  }
}




// Lists on Backgrounds
// --------------------------------------------
.#{$css-prefix}__items--pad, %list__items--pad {
  padding: $spacer-small;
}

.#{$css-prefix}list__items--bg--dark {
  @extend %list__items--pad;
  background-color: $ui-dark;
}

.#{$css-prefix}list__items--bg--orange {
  @extend %list__items--pad;
  background-color: $ui-orange;
}



// Base List Item Styles
// --------------------------------------------
.#{$css-prefix}list__item, %list__item {
  // padding: 0.5rem 0;
}



// List Item Reverse
// --------------------------------------------
.#{$css-prefix}list__item--reverse {
  @extend %list__item;
  color: $ui-white;
}



// List Item Link
// --------------------------------------------
.#{$css-prefix}list__link {
  color: inherit;
}








// Block Quotes
// --------------------------------------------
.#{$css-prefix}blockquote {
  display: block;
  border-left: 5px solid $ui-gray;
  padding: 0.5rem 1rem;
  font-style: italic;
  quotes: "\201C""\201D""\2018""\2019";

  &:before {
    color: $ui-gray;
    content: open-quote;
  }

  &:after {
    color: $ui-gray;
    content: close-quote;
  }
}




// Inline Text
// --------------------------------------------
strong { font-weight: $font-weight-bold; }

em { font-style: italic; }

small { font-size: $body-size-xsmall; }

mark {
  background-color: $ui-orange;
  color: $ui-white;
}

strike {
  text-decoration: line-through;
  color: $ui-gray;
}




// Helper Classes
// --------------------------------------------
.u-text--uppercase { text-transform: uppercase; }

.u-text--lowercase { text-transform: lowercase; }

.u-text-alignment--left { text-align: left; }

.u-text-alignment--center { text-align: center; }

.u-text-alignment--right { text-align: right; }

.u-text--bold { font-weight: $font-weight-bold; }

.u-text-overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}


