.#{$css-prefix}btn--default { @include button($btn-bg-default); }
.#{$css-prefix}btn--white { @include button($btn-bg-white); }
.#{$css-prefix}btn--primary { @include button($btn-bg-primary); }
.#{$css-prefix}btn--secondary { @include button($btn-bg-secondary); }
// .#{$css-prefix}btn--tertiary { @include button($btn-bg-tertiary); }
.#{$css-prefix}btn--danger { @include button($btn-bg-danger); }
.#{$css-prefix}btn--confirm { @include button($btn-bg-confirm); }

